import React from 'react'
import graphql from 'babel-plugin-relay/macro'
import QueryRenderer from 'Components/QueryRenderer'

const query = graphql`
  query ViewMegladronQuery{
    megladron {
      docs {
        _id
        title
      }
    }
  }
`

function ViewMegladron(props) {
  return (
    <QueryRenderer query={query} component={data => <ViewMegladronComponent {...data} />}/>
  )
}

function ViewMegladronComponent(props) {
  console.log(props)
  return (
    <div>View</div>
  )
}

export default ViewMegladron