import {
  Environment,
  Network,
  RecordSource,
  Store,
  QueryResponseCache
} from 'relay-runtime';
import ErrorUtils from './ErrorUtils';
import AuthService from './AuthService';

const oneMinute = 60 * 1000;

//Because rarely a data would chnage on sit in one hour
const cache = new QueryResponseCache({ size: 250, ttl: oneMinute * 60 });


