function fetchQuery(
  operation,
  variables,
  cacheConfig
) {

  const queryID = operation.text;
  const isMutation = operation.operationKind === 'mutation';
  const isQuery = operation.operationKind === 'query';
  const forceFetch = cacheConfig && cacheConfig.force;

  // Try to get data from cache on queries
  const fromCache = cache.get(queryID, variables);
  if (
    isQuery &&
    fromCache !== null &&
    !forceFetch
  ) {
    return fromCache;
  }

  let extraHeaders = {}

  if(AuthService.isUserLoggedIn()){
    extraHeaders = {
      "authorization": "Bearer "+AuthService.getAccessToken(),
      "refreshToken": AuthService.getRefreshToken()
    }
  }
  //console.log(extraHeaders)

  const axiosConfig = {
    headers: {
      'Content-Type': 'application/json',
      ...extraHeaders
    },
    method: "POST",
  };
    axiosConfig.data = JSON.stringify({
      query: operation.text,
      variables,
    })
  let url = "https://gaming-stars.herokuapp.com/graphql"
  let env = document.querySelector("[name=environment]").value
  if(env === "Production") {
    url = "https://www.gamingstars.in/graphql"
  }
  if(process.env.NODE_ENV !== 'production'){
    url = "http://localhost:5000/graphql"
  }

  console.log("Will be sending request to ", url)
  return fetch(url, {
    method: 'POST',
    headers: {
      // Add authentication and other headers here
      'content-type': 'application/json',
      ...extraHeaders
    },
    body: JSON.stringify({
      query: operation.text, // GraphQL text from input
      variables,
    }),
  }).then(response => {
    return response.json();
  }).then(json => {
    // Update cache on queries
    // if (isQuery && json) {
    //   cache.set(queryID, variables, json);
    // }
    // // Clear cache on mutations
    // if (isMutation) {
    //   cache.clear();
    // }

    return json;
  });
}

const environment = new Environment({
  network: Network.create(fetchQuery),
  store: new Store(new RecordSource()),
});

export default environment;